body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

::root {
  --bg: #242526;
  --bg-accent: #484a4d;
  --text-color: #dadce1;
  --nav-size: 60px;
  --border: 1px solid #474a4d;
  --border-radius: 8px;
  --speed: 500ms;
}

a {
  color: #dadce1;
  text-decoration: none;
}

.navbar {
  margin: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);

  border-radius: 3px;
  text-align: center;
  width: 40%;
  padding: 100px 0;
  background-color: rgb(233, 229, 229);
  align-items: center;
}

.navbar-nav {
  width: 135px;
  height: 40px;
  position: fixed;
  top: 60%;
  left: 26%;
  margin-top: 40px;
  font-weight: 400;
  border-radius: 10px;
  border: none;
  cursor: pointer;
  padding: 10px 20px;
  font-size: 20;
  background-color: rgb(209, 209, 209);
  outline: none;
}

.nav-item {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.icon-button {
  width: 135px;
  height: 40px;
  background-color: #484a4d;
  padding: 10px 20px;
  margin: 2px;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: filter 300ms;
}

.icon-button:hover {
  filter: brightness(1.2);
}

.dropdown {
  position: absolute;
  top: 58px;
  width: 300px;
  transform: translateX(-45%);
  background-color: #242526;
  border: 1px solid #474a4d;
  border-radius: 8px;
  padding: 1rem;
  overflow: hidden;
}

.menu-item {
  height: 50px;
  display: flex;
  align-items: center;
  border-radius: 8px;
  transition: background 300ms;
  padding: 0.5rem;
}

.menu-item:hover {
  background-color: #525357;
}

.icon-right {
  margin-left: auto;
}

.clicky {
  width: 135px;
  height: 40px;
  position: fixed;
  top: 60%;
  left: 26%;
  margin-top: 40px;
  font-weight: 400;
  border-radius: 10px;
  border: none;
  cursor: pointer;
  padding: 10px 20px;
  font-size: 20;
  background-color: rgb(209, 209, 209);
  overflow: hidden;
  outline: none;
}

.nextbtn {
  width: 135px;
  height: 40px;
  position: fixed;
  top: 60%;
  left: 51%;
  margin-top: 40px;
  font-weight: 400;
  border-radius: 10px;
  border: none;
  cursor: pointer;
  padding: 10px 20px;
  font-size: 20;
  background-color: rgb(209, 209, 209);
  overflow: hidden;
  outline: none;
}

.nextbtn {
  width: 135px;
  height: 40px;
  position: fixed;
  top: 60%;
  left: 51%;
  margin-top: 40px;
  font-weight: 400;
  border-radius: 10px;
  border: none;
  cursor: pointer;
  padding: 10px 20px;
  font-size: 20;
  background-color: rgb(209, 209, 209);
  overflow: hidden;
  outline: none;
}

.share {
  width: 135px;
  height: 40px;
  position: fixed;
  top: 60%;
  left: 26%;
  margin-top: 40px;
  font-weight: 400;
  border-radius: 10px;
  border: none;
  cursor: pointer;
  padding: 10px 20px;
  font-size: 20;
  background-color: rgb(209, 209, 209);
  overflow: hidden;
  outline: none;
}

.nextbtn::before,
.share::before {
  content: "";
  position: absolute;
  left: 0;
  width: 100%;
  height: 0%;
  background: #333;
  z-index: -1;
  transition: 0.5s;
  bottom: 0;
  border-radius: 50% 50% 0 0;
  outline: none;
}

.nextbtn:hover::before,
.share:hover::before {
  height: 180%;
  outline: none;
}

.nextbtn:hover,
.share:hover {
  color: #fff;
  outline: none;
}

.nextbtn:active,
.share:active {
  border: none;
}

a {
  color: inherit;
}

a {
  text-decoration: none;
}
a:link,
a:visited {
  color: inherit;
}
a:hover {
  color: inherit;
}

@import url("https://fonts.googleapis.com/css2?family=Bitter&display=swap");

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Bitter", serif;
  font-size: 1rem;
}

body {
  line-height: 1.4;
}

a {
  color: #333;
}

.quote-box {
  margin: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);

  border-radius: 3px;
  text-align: center;
  width: 40%;
  padding: 100px 0;
  background-color: rgb(233, 229, 229);
}

.quote {
  margin-top: 0px;
  padding-top: 0px;
  margin: auto;

  font-size: 1.1rem;
  width: 80%;
}

.author {
  margin: auto;
}

.quote-box {
  align-items: center;
}
